* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100%;
  height: 100vh;
  margin: 0;
  background: linear-gradient(90deg, #800080, #ffc0cb);
  color: #f5f6f7;
  font-family: Tahoma;
  font-size: 1.5rem;
}

h1 {
  margin: 1em auto;
  text-align: center;
}
p {
  margin: 2em 0;
}
p#description {
  width: 50em;
  max-width: 60%;
  margin: 2em auto;
  text-align: left;
}

form {
  width: 60vw;
  max-width: 500px;
  min-width: 300px;
  margin: 0 auto;
  padding-bottom: 2em;
}

input,
textarea,
select {
  margin: 0 10px 0 0;
  width: 100%;
  min-height: 2em;
}

input,
textarea {
  background-color: whitesmoke;
  border: 1px solid #0a0a23;
  color: #000000;
}

.inline {
  width: unset;
  margin: 0 0.5em 0 0;
  vertical-align: middle;
}
label {
  display: block;
}

button {
  display: block;
  width: 60%;
  margin: 1em auto;
  height: 2em;
  font-size: 1.1rem;
  background-color: #c710c7cb;
  color: white;
  border-color: pink;
  min-width: 300px;
  margin-top: 2em;
  text-transform: uppercase;
  &:hover {
    background-color: whitesmoke;
    color: magenta;
    border: 1px wheat black;
  }
  &:active,
  :focus {
    background-color: rgba(177, 40, 97, 0.785);
  }
}

input[type="file"] {
  padding: 1px 2px;
}
a {
  color: #dfdfe2;
}
.domain-select {
  margin: 1.5em 0;
}
.checkbox-container {
  margin-bottom: 2em;
}
